// auth.js

// Wait for the DOM to load before running scripts
document.addEventListener('DOMContentLoaded', () => {
    // Toggle password visibility for signup
    const togglePasswordSignUp = document.getElementById('togglePasswordSignUp');
    const passwordInputSignUp = document.getElementById('password');
    if (togglePasswordSignUp) {
        togglePasswordSignUp.addEventListener('click', function () {
            const type = passwordInputSignUp.getAttribute('type') === 'password' ? 'text' : 'password';
            passwordInputSignUp.setAttribute('type', type);
            this.classList.toggle('fa-eye-slash');
        });
    }

    // Toggle password visibility for confirm password
    const toggleConfirmPassword = document.getElementById('toggleConfirmPassword');
    const confirmPasswordInput = document.getElementById('confirmPassword');
    if (toggleConfirmPassword) {
        toggleConfirmPassword.addEventListener('click', function () {
            const type = confirmPasswordInput.getAttribute('type') === 'password' ? 'text' : 'password';
            confirmPasswordInput.setAttribute('type', type);
            this.classList.toggle('fa-eye-slash');
        });
    }

    // Handle signup
    document.getElementById('signupBtn').addEventListener('click', function (event) {
        event.preventDefault(); // Prevent form submission
        handleSignup();
    });

    // Handle login
    document.getElementById('loginForm').addEventListener('submit', handleLogin);
});

// Function to handle signup
function handleSignup() {
    const firstName = document.getElementById('firstName');
    const lastName = document.getElementById('lastName');
    const password = document.getElementById('password');
    const confirmPassword = document.getElementById('confirmPassword');

    // Clear previous error messages
    clearErrorMessages();

    let isValid = true;

    // Validate fields
    if (!firstName.value.trim()) {
        showError(firstName, 'Please fill this field');
        isValid = false;
    }
    if (!lastName.value.trim()) {
        showError(lastName, 'Please fill this field');
        isValid = false;
    }
    if (!password.value.trim()) {
        showError(password, 'Please fill this field');
        isValid = false;
    }
    if (!confirmPassword.value.trim()) {
        showError(confirmPassword, 'Please fill this field');
        isValid = false;
    } else if (password.value !== confirmPassword.value) {
        showError(confirmPassword, 'Passwords do not match');
        isValid = false;
    }

    if (isValid) {
        // Store user details in localStorage
        const user = { firstName: firstName.value, lastName: lastName.value, password: password.value };
        localStorage.setItem('user', JSON.stringify(user));

        // Display signup success message
        const successMessage = document.getElementById('successMessage');
        successMessage.textContent = 'Signup successful!';
        successMessage.style.display = 'block';

        // Fade out success message after a delay
        setTimeout(() => {
            successMessage.style.opacity = 0;
            setTimeout(() => successMessage.style.display = 'none', 1000);
        }, 3000);

        document.getElementById('signupForm').reset();
    }
}

// Function to handle user login
function handleLogin(event) {
    event.preventDefault(); // Prevent form submission
    const username = document.getElementById('name').value.trim();
    const password = document.getElementById('password').value;
    const users = JSON.parse(localStorage.getItem('users')) || []; // Retrieve all users

    // Clear previous error messages
    document.getElementById('usernameError').textContent = '';
    document.getElementById('passwordError').textContent = '';

    let isValid = true;

    // Validate fields
    if (!username) {
        document.getElementById('usernameError').textContent = 'Please fill this field';
        isValid = false;
    }
    if (!password) {
        document.getElementById('passwordError').textContent = 'Please fill this field';
        isValid = false;
    }

    // If validation fails, return early
    if (!isValid) return;

    // Check if the user exists and credentials match
    const user = users.find(user => user.firstName === username && user.password === password); // Find user

    if (user) {
        const successMessage = document.createElement('p');
        successMessage.textContent = 'Login successful';
        successMessage.classList.add('success-message');
        document.querySelector('.container').prepend(successMessage);
        // Redirect to main.html after a delay
        setTimeout(() => {
            window.location.href = 'main.html';
        }, 1000); // Redirect after 1 second
    } else {
        document.getElementById('passwordError').textContent = 'Invalid credentials'; // Invalid credentials
    }
}

// Utility functions for showing and clearing error messages
function showError(inputElement, message) {
    const errorDiv = document.createElement('div');
    errorDiv.className = 'error-message';
    errorDiv.textContent = message;
    errorDiv.style.color = 'red';
    errorDiv.style.fontSize = '12px';
    errorDiv.style.marginTop = '5px';

    inputElement.style.borderColor = 'red'; // Add red border for error
    inputElement.parentElement.appendChild(errorDiv);
}

function clearErrorMessages() {
    const errorMessages = document.querySelectorAll('.error-message');
    errorMessages.forEach(msg => msg.remove());
}
